<template>
    <div>
        <h2>Lista de Pokemon</h2>
        <p v-for="pk in pokemonArray">{{ pk.name }}</p>
    </div>
</template>
  
<script>
import axios from 'axios'
export default {
    name: 'List',
    data: function () {
        return {
            name: "",
            image: "",
            pokemonArray: {
                type: Array
            },
        }
    },
    methods: {
        fetchPokemon() {
            axios.get(`https://pokeapi.co/api/v2/pokemon?limit=100000&offset=0`)
                .then(resp => {
                    pokemonArray = resp.data;
                })
                .catch(error => {
                    console.log(error)
                })
        }
    },
    created() {
        this.fetchPokemon();
    }
}
</script>
  
